<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>
        注册新用户
        <span class="go"
          >我有账号，去 <a href="login.html" target="_blank">登陆</a>
        </span>
      </h3>
      <div class="content">
        <label>手机号:</label>
        <input
          type="text"
          placeholder="请输入你的手机号"
          v-model="phone"
          name="phone"
          v-validate="{ required: true, regex: /^1[\d]{10}$/ }"
          :class="{ error: errors.has('phone') }"
        />
        <span class="error-msg">{{ errors.first("phone") }}</span>
      </div>
      <div class="content">
        <label>验证码:</label>
        <input
          type="text"
          placeholder="请输入验证码"
          v-model="verify"
          name="verifyCode"
          v-validate="{ required: true, regex: /^\d{6}$/ }"
          :class="{ error: errors.has('verifyCode') }"
        />
        <button @click="getVerifyCode" :disabled="isflag">
          获取验证码<span>{{ time }}s</span>
        </button>
        <span class="error-msg">{{ errors.first("verifyCode") }}</span>
      </div>
      <div class="content">
        <label>登录密码:</label>
        <input
          type="text"
          placeholder="请输入你的登录密码"
          v-model="password"
          name="password"
          v-validate="{ required: true, regex: /.{3,}/ }"
          :class="{ error: errors.has('password') }"
        />
        <span class="error-msg">{{ errors.first("password") }}</span>
      </div>
      <div class="content">
        <label>确认密码:</label>
        <input
          type="text"
          placeholder="请输入确认密码"
          v-model="passwordAgain"
          name="passwordAgain"
          v-validate="{ required: true, is: password }"
        />
        <span class="error-msg">{{ errors.first("passwordAgain") }}</span>
      </div>
      <div class="controls">
        <input
          type="checkbox"
          v-model="isAgree"
          name="agree"
          v-validate="{ agree: '' }"
        />
        <span>同意协议并注册《尚品汇用户协议》</span>
        <span class="error-msg">{{ errors.first("agree") }}</span>
      </div>
      <div class="btn">
        <button @click="completeRegister">完成注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号</div>
    </div>
  </div>
</template>

<script>
import { reqVerifyCode, reqCompleteRegister } from "@/api";
export default {
  name: "Register",
  data() {
    return {
      phone: "",
      verify: "",
      time: 5,
      isflag: false,
      password: "",
      passwordAgain: "",
      isAgree: false,
    };
  },
  methods: {
    // 获取手机验证码
    async getVerifyCode() {
      this.isflag = true;
      try {
        // 手机正则验证
        const phoneReg = /^1[\d]{10}$/g;
        if (!phoneReg.test(this.phone)) {
          alert("你的手机格式不对");
          this.isflag = false;
          return;
        }
        // 发送手机验证码
        let res = await reqVerifyCode(this.phone);
        this.verify = res;
        // 按钮节流 禁用操作
        const timer = setInterval(() => {
          if (this.time-- <= 1) {
            this.time = 5;
            clearInterval(timer);
            this.isflag = false;
          }
        }, 1000);
      } catch (error) {
        alert(error.message);
        this.isflag = false;
      }
    },
    // 完成注册
    async completeRegister() {
      // 表单校验
      let res = await this.$validator.validateAll();
      if (!res) return this.$message.error("你的格式不对，请检查你的格式");

      const { phone, verify, password, passwordAgain, isAgree } = this;

      if (
        !(
          phone &&
          verify &&
          password &&
          passwordAgain &&
          isAgree &&
          password === passwordAgain
        )
      ) {
        alert("格式不对");
        return;
      }

      try {
        let res = await reqCompleteRegister({ phone, password, code: verify });
        if (res === null) alert("注册成功");
        this.$router.push(`/login`);
      } catch (error) {
        alert(error.message);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.error {
  border: 1px solid red !important;
}

.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>